body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #5CDB95
}

.Title {
  text-align: center;
}

ul {
  list-style-type: none;
}

.Content {
  margin-top: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2em;
  background: #379683;
  color: #EDF5E1;
}

.Skills {
  display: -webkit-flex;
  display: flex;
}

.Attributes {
  display: -webkit-flex;
  display: flex;
}

h1 {
  color: #05386B;
}

input {
  border: 2px solid black;
  color: transparent;
  text-shadow: 0 0 0 #EDF5E1;
  text-align: center;
  font-size: 0.75em;
  background-color: #05386B;
}

input:focus {
  outline: none;
}

#btns {
  text-align: center;
}

button {
  margin-top: 50px;
  margin-left: 20px;
  margin-right: 20px;
  background-color: #05386B; /* Green */
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border: solid black 1px;
  color: #EDF5E1;
}

button:focus {
  outline: none;
}
footer {
    display: -webkit-flex;
    display: flex;
    position: fixed;
    z-index: 2;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    background-color: #05386B;
    color: white;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
}

a {
    text-decoration: none;
    color: #EDF5E1;
    font-size: 1.25rem;
}

.fab {
    margin-right: 10px;
    margin-left: 10px;
}
